import Friend from './Friend';

function FriendsList({ friends }) {
  return (
    <ul>
      {friends.map(friend => (
        <li key={friend.id}>
          <Friend
            name={friend.name}
            avatar={friend.avatar}
            status={friend.isOnline}
          />
        </li>
      ))}
    </ul>
  );
}

export default FriendsList;
